body {
  font-family: Arial, sans-serif;
  margin: 20px;
  background-color: #121212;
  color: #ffffff;
}
.container {
  width: 80%;
  margin: auto;
  background-color: #1e1e1e;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
textarea {
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  background-color: #333333;
  color: #ffffff;
  border: 1px solid #444444;
  border-radius: 4px;
}
button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #6200ea;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  transition: background-color 0.3s;
}
button:hover {
  background-color: #3700b3;
}
.formatted-output {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #444444;
  background-color: #333333;
  color: #ffffff;
  border-radius: 4px;
}